import React from "react";
import styled from "styled-components";

export const Input = ({ type, status, label}) => {
    switch (status) {
        case "success":
            status ='input-success';
            break;
        case "info":
            status ='input-info';
            break;
        case "alert":
            status ='input-alert';
            break;
        case "warning":
            status ='input-warning';
            break; 
        default:
            break;
    }
    return(
        <UIinput
            type={type}
            className={`input ${status}`}
        >
            {label}
        </UIinput>
    )
}

const UIinput = styled.input``;

